import React, { useState } from 'react';
import { Row, Col, Input, Button, Form, Checkbox, Typography, Space, Modal } from "antd";
import { useDispatch, useSelector } from "react-redux";
import { Redirect } from "react-router-dom";

import { signInWithGoogle, signInWithFacebook } from "config/firebase-db";
import LoginFormStyle from "./style";

const LoginForm = () => {

    const { Text } = Typography;
    const [rememberMe, setRememberMe] = useState(true);
    const user = useSelector(state => state.user);

    const dispath = useDispatch();

    const loginSuccess = (user) => {
        // console.log(user);
        dispath({ type: 'USER_LOGGED_IN', payload: user })
    }

    const loginFailed = (error) => {
        // console.log('Error when logged in: ', error);
        Modal.error({
            title: 'An error has occured when login',
            content: (
                <p>{error.message}</p>
            )
        })
    }

    if (user && user.isLogged) {
        return <Redirect to="/" />;
    } else {
        return (
            <Row >
                <Col style={LoginFormStyle.root} span={8} offset={8} >
                    <Text style={LoginFormStyle.title}>ng k</Text>
                    <Space direction="vertical" style={{border: '20px'}}>
                        <Button style={{...LoginFormStyle.quickLoginBtn, ...LoginFormStyle.phoneLoginBtn }}>S IN THOI</Button>
                        <Button onClick={() => signInWithFacebook(loginSuccess, loginFailed)} style={{...LoginFormStyle.quickLoginBtn, ...LoginFormStyle.facebookLoginBtn}}>Facebook</Button>
                        <Button onClick={() => signInWithGoogle(loginSuccess, loginFailed)} style={{...LoginFormStyle.quickLoginBtn, ...LoginFormStyle.googleLoginBtn}}>Google</Button>
                    </Space>
                    <Text style={{margin: '20px 0'}}>Hoc ng nhp bng ti khon ca bn</Text>
                    <Form>
                        <Form.Item name="username">
                            <Input prefix="" placeholder="Username/Email" />
                        </Form.Item>
                        <Form.Item name="password">
                            <Input.Password prefix="" placeholder="Password" />
                        </Form.Item>
                        <Form.Item>
                            <Checkbox checked={rememberMe} onClick={() => setRememberMe(!rememberMe)}>Lu thng tin ng nhp</Checkbox>
                            <a href='/forgot-password' style={LoginFormStyle.forget}>Qun mt khu?</a>
                        </Form.Item>
                        <Form.Item>
                            <Button style={LoginFormStyle.loginBtn} htmlType="submit">
                                NG NHP
                            </Button>
                        </Form.Item>
                    </Form>
                    <Text style={LoginFormStyle.policy}>
                        Chng ti khng s dng thng tin ca bn vi bt k mc ch no. Bng cch ng nhp hoc ng k, bn ng  vi <a href='/policy'>Chnh sch quy nh ca ...</a>
                    </Text>
                </Col>
            </Row>
        )
    }

    
}

export default LoginForm;