const RestaurantInfo = () => {

    const { RestaurantInfoStyle } = RestaurantContentStyle;

    const { Title } = Typography;

    return (
        <div style={RestaurantInfoStyle.root}>
            <Breadcrumb>
                <Breadcrumb.Item><Link to={{pathname: "../restaurant"}}>Ca hng</Link></Breadcrumb.Item>
                <Breadcrumb.Item style={RestaurantInfoStyle.breadcrumb}>Thng tin</Breadcrumb.Item>
            </Breadcrumb>
            <div style={RestaurantInfoStyle.infomation}>
                {data.map(item => {
                    return (
                        <>
                        <Info Item={item} />
                        </>
                    )
                })}
            </div>
            <div style={{marginTop: 40}}>
                <Title level={4}>Cc ca hng cn duyt</Title>
                <Space size={50}>
                    {anotherData.map(item => {
                        return (
                            <>
                            <AnotherRestaurant Item={item} />
                            </>
                        )
                    })}
                </Space>
            </div>
        </div>
    )
}

export default RestaurantInfo;