const OrderContent = () => {

    const [state, setState] = useState({});

    const getColumnSearchProps = dataIndex => ({
        filterDropdown: ({ setSelectedKeys, selectedKeys, confirm, clearFilters }) => (
            <div style={{ padding: 8 }}>
                <Input
                    placeholder={`Search ${dataIndex}`}
                    value={selectedKeys[0]}
                    onChange={e => setSelectedKeys(e.target.value ? [e.target.value] : [])}
                    onPressEnter={() => handleSearch(selectedKeys, confirm, dataIndex)}
                    style={{ width: 188, marginBottom: 8, display: 'block' }}
                />
                <Space>
                    <Button
                        type="primary"
                        onClick={() => handleSearch(selectedKeys, confirm, dataIndex)}
                        icon={<SearchOutlined />}
                        size="small"
                        style={{ width: 90 }}
                    >
                        Search
                    </Button>
                    <Button onClick={() => handleReset(clearFilters)} size="small" style={{ width: 90 }}>
                        Reset
                    </Button>
                </Space>
            </div>
        ),
        filterIcon: filtered => <SearchOutlined style={{ color: filtered ? '#1890ff' : undefined }} />,
        onFilter: (value, record) =>
          record[dataIndex]
            ? record[dataIndex].toString().toLowerCase().includes(value.toLowerCase())
            : '',
        onFilterDropdownVisibleChange: visible => {
            if (visible) {
                // setTimeout(() => searchInput.select(), 100);
            }
        },
        render: text =>
            state.searchedColumn === dataIndex ? (
                <Highlighter
                    highlightStyle={{ backgroundColor: '#ffc069', padding: 0 }}
                    searchWords={[state.searchText]}
                    autoEscape
                    textToHighlight={text ? text.toString() : ''}
                />
            ) : (
                text
            ),
    });

    const handleSearch = (selectedKeys, confirm, dataIndex) => {
        confirm();
        setState({
            searchText: selectedKeys[0],
            searchedColumn: dataIndex,
        });
    };
    
    const handleReset = clearFilters => {
        clearFilters();
        setState({ searchText: '' });
    };

    const columns = [
        {
            title: "M n hng",
            dataIndex: "orderID",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('orderID'), 
        },
        {
            title: "Ngi t",
            dataIndex: "orderUser",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('orderUser'), 
        },
        {
            title: "Tn qun",
            dataIndex: "restaurantName",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('restaurantName'), 
        },
        {
            title: "Tn ngi giao hng",
            dataIndex: "shipperName",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('shipperName'), 
        },
        {
            title: "a ch khch hng",
            dataIndex: "userAddress",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('userAddress'), 
        },
        {
            title: "Thnh ph",
            dataIndex: "city",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('city'), 
        },
        {
            title: "Thi gian t",
            dataIndex: "orderTime",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('orderTime'), 
        },
        {
            title: "Thi gian giao d kin",
            dataIndex: "shipTime",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('shipTime'), 
        },
        {
            title: "Trng thi",
            key: "orderStatus",
            dataIndex: "orderStatus",
            render: (orderStatus) => {
                const color = (orderStatus === " giao") ? "success" : (orderStatus === "Cha giao") ? "processing" : "error";
                // console.log(color);
                return (
                    <Tag color={color}>
                        {orderStatus.toUpperCase()}
                    </Tag>
                );
            },
            className: "tableOrderContentStyle",
        },
        {
            title: "Tin giao hng",
            dataIndex: "deliveryMoney",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('deliveryMoney'), 
        },
        {
            title: "Tin gim gi",
            dataIndex: "discountMoney",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('discountMoney'), 
        },
        {
            title: "Tng tin",
            dataIndex: "totalMoney",
            className: "tableOrderContentStyle",  
            ...getColumnSearchProps('totalMoney'), 
        },
    ];

    return (
        <div style={OrderContentStyle.root}>
            <Breadcrumb>
                <Breadcrumb.Item href="user">Ngi dng</Breadcrumb.Item>
                <Breadcrumb.Item href="" style={OrderContentStyle.breadcrumb}>Thng tin</Breadcrumb.Item>
            </Breadcrumb>
            <Table style={OrderContentStyle.table} bordered={true} dataSource={data} columns={columns} size="small" pagination={{position: ['bottomCenter']}} />
        </div>
    )
}

export default OrderContent;