import React from "react";
import { Row, Col, Breadcrumb, Image, Typography, Radio, Button } from "antd";
import { EnvironmentTwoTone } from "@ant-design/icons";

import CheckOutStyle from "./style.js";
import '../cart/tableStyle.css'

const data = [
    {
        foodName: 'Khoai ty chin',
        foodImg: 'KhoaiTayChien.png',
        restaurantName: 'PL Coffee & Tea',
        price: '20.000 VN',
        numberOrder: '1',
        total: '20.000 VN'
    },
    {
        foodName: 'Xc xch chin',
        foodImg: 'XucXichChien.png',
        restaurantName: 'PL Coffee & Tea',
        price: '20.000 VN',
        numberOrder: '2',
        total: '40.000 VN'
    },
    {
        foodName: 'Nem Chua Rn',
        foodImg: 'NemChuaRan.png',
        restaurantName: 'PL Coffee & Tea',
        price: '20.000 VN',
        numberOrder: '1',
        total: '20.000 VN'
    },
]

const CheckOut = () => {

    const { Text } = Typography; 

    const [value, setValue] = React.useState(1);

    const onChange = e => {
        console.log('radio checked', e.target.value);
        setValue(e.target.value);
    };

    return (
        <Row>
            <Col span={16} offset={4}>
                <Breadcrumb>
                    <Breadcrumb.Item>Trang ch</Breadcrumb.Item>
                    <Breadcrumb.Item href="" style={CheckOutStyle.breadcrumb}>Thanh ton</Breadcrumb.Item>
                </Breadcrumb>
                <div style={CheckOutStyle.address}>
                    <div style={{display: "inline-flex"}}>
                        <EnvironmentTwoTone style={{fontSize: "24px"}} twoToneColor="#7FAD39" />
                        <p style={CheckOutStyle.addressTitle}>a ch nhn hng</p>
                    </div>  
                    <div style={CheckOutStyle.addressInfo}>
                        <div style={CheckOutStyle.nameText}>
                            <Text>L Quang Dut</Text><br/>
                            <Text>(+84) 357898469</Text>
                        </div>
                        <Text style={CheckOutStyle.addressText}>K tc x khu V ca Trng i hc Cng ngh thng tin v truyn thng Vit - Hn, ng Trn i Ngha, Phng Ha Qu, Qun Ng Hnh Sn,  Nng</Text>
                        <Text style={CheckOutStyle.changeText}>Thay i</Text>
                    </div>
                </div>
                <table>
                    <thead>
                        <th>Sn phm</th>
                        <th>Ca hng</th>
                        <th>Gi tin</th>
                        <th style={{textAlign: "center"}}>S lng</th>
                        <th>Thnh tin</th>
                    </thead>
                    {data.map(item => {
                        return (
                            <>
                            <tr>
                                <td>
                                    <Image width="100px" height="100px" src={"images/restaurant/menu/" + item.foodImg} preview={false} ></Image>
                                    <Text style={CheckOutStyle.foodName}>{item.foodName}</Text>
                                </td>
                                <td>{item.restaurantName}</td>
                                <td style={CheckOutStyle.price}>{item.price}</td>
                                <td style={CheckOutStyle.numberOrder}>{item.numberOrder}</td>
                                <td style={CheckOutStyle.price}>{item.total}</td>
                            </tr>
                            </>
                        )
                    })}
                </table>
                <div style={CheckOutStyle.messAndDiscount}>
                    <div style={CheckOutStyle.mess}>
                        <p style={CheckOutStyle.messTitle}>Li nhn:</p>
                        <input style={CheckOutStyle.messInput} placeholder="Lu  cho ngi bn" />
                    </div>
                    <div style={CheckOutStyle.discount}>
                        <p style={CheckOutStyle.messTitle}>M gim gi:</p>
                        <div>
                            <p style={CheckOutStyle.discountCode}>M: KHJHFJE5</p>
                            <p style={CheckOutStyle.discountChange}>Thay i</p>
                        </div>
                        <p style={CheckOutStyle.discountCode}>Gim 30.000 VND gi tr n hng</p>
                    </div>
                </div>
                <div style={CheckOutStyle.payment}>
                    <div style={CheckOutStyle.method}>
                        <p style={CheckOutStyle.messTitle}>Phng thc thanh ton <span style={{color: "#FF0000"}}>*</span></p>
                        <Radio.Group onChange={onChange} value={value}>
                            <Radio style={CheckOutStyle.radioStyle} value={1}>
                                Thanh ton khi nhn hng
                            </Radio>
                            <Radio style={CheckOutStyle.radioStyle} value={2}>
                                Thanh ton qua Internet Banking
                            </Radio>
                        </Radio.Group>
                    </div>
                    <div style={CheckOutStyle.billForm}>
                        <p style={CheckOutStyle.billTitle}>Thanh ton</p>
                        <div>
                            <p style={CheckOutStyle.billText}>Tm tnh</p>
                            <p style={CheckOutStyle.amountText}>80.000 VND</p>
                        </div>
                        <div>
                            <p style={CheckOutStyle.billText}>Ph vn chuyn (3km)</p>
                            <p style={CheckOutStyle.amountText}>30.000 VND</p>
                        </div>
                        <div>
                            <p style={CheckOutStyle.billText}>Gim gi</p>
                            <p style={CheckOutStyle.amountText}>-30.000 VND</p>
                        </div>
                        <div style={CheckOutStyle.bd}>
                            <p style={CheckOutStyle.billText}>Tng n hng</p>
                            <p style={CheckOutStyle.amountText}>80.000 VND</p>
                        </div>
                        <Button style={CheckOutStyle.checkoutButton}>Tin hnh thanh ton</Button>
                    </div>
                </div>
            </Col>
        </Row>
    )
}

export default CheckOut;