import React from "react";
import { Button, Image, Form, Input, Layout, Checkbox, Typography } from 'antd';
import { UserOutlined, LockOutlined } from "@ant-design/icons";

import { signInWithGoogle } from "config/firebase-db";

const MerchantLogin = () => {

    const { Header, Content } = Layout;

    return (
        <Layout style={{height: '100%', backgroundImage: 'url(images/background.jpg)'}}> 
            <Header style={{backgroundColor: "white", textAlign: 'center', color: '#7FAD39', fontSize: 36}}>QUN L CA HNG CA BN</Header>
            <Content style={{height: 'calc(100vh - 64px)', display: 'flex', justifyContent: 'center', alignItems: 'center'}}>
                <div style={{display: 'flex', flexDirection: 'column', alignItems: 'center', alignContent: 'center', justifyContent: 'center', backgroundColor: '#fff', width: 600, borderRadius: 10, padding: '18px 30px 25px'}}>
                    <h1>ng nhp</h1>
                    <Typography.Paragraph style={{textAlign: 'center'}}>ng nhp bng ti khon ca bn bng s in thoi, email hoc tn ti khon  c chng ti xc nhn l ca hng i tc.</Typography.Paragraph>
                    <Form
                        initialValues={{ remember: true}}
                    >
                        <Form.Item>
                            <Input prefix={<UserOutlined />} placeholder="Email hoc S in thoi" />
                        </Form.Item>
                        <Form.Item>
                            <Input.Password prefix={<LockOutlined />} placeholder="Mt khu" />
                        </Form.Item>
                        <Form.Item name="remember" valuePropName="checked">
                            <Checkbox checked>Lu thng tin ng nhp</Checkbox>
                            <Typography.Link>
                                Qun mt khu?
                            </Typography.Link>
                        </Form.Item>
                        <Form.Item style={{marginBottom: 8}}>
                            <Button style={{width: 540, height: 35, background: '#7FAD39', color: '#fff'}}>NG NHP</Button>
                        </Form.Item>
                        <Form.Item>
                            <Button onClick={signInWithGoogle} style={{width: 540, height: 35, background: '#DB4437', color: '#fff'}} icon={<Image width={30} preview={false} src="images/google.png" />}>GOOGLE</Button>
                        </Form.Item>
                        <Typography.Paragraph style={{textAlign: 'center'}}>
                            Chng ti khng s dng thng tin ca bn vi bt k mc ch no. Bng cch ng nhp hoc ng k, bn ng  vi Chnh sch quy nh ca ...
                        </Typography.Paragraph>
                    </Form>
                </div>
            </Content>
        </Layout>
    )

}

export default MerchantLogin;