import React from "react";
import { Row, Col, Breadcrumb, notification, Form, Input, Select, Cascader, Checkbox, Button } from "antd";

import CheckOutStyle from "./style.js";

const AddAddress = () => {

    const { Option } = Select;

    const openNotification = () => {
        notification.warning({
            message: "Thng bo",
            description: "Bn cha c a ch mc nh, vui lng thm a ch  ngi vn chuyn c th giao hng cho bn.",
            duration: 10
        });
    };
    openNotification();

    const [form] = Form.useForm();

    const formItemLayout = {
        labelCol: {
            xs: { span: 24 },
            sm: { span: 8 },
        },
        wrapperCol: {
            xs: { span: 24 },
            sm: { span: 16 },
        },
    };
      const tailFormItemLayout = {
        wrapperCol: {
            xs: {
                span: 24,
                offset: 0,
            },
            sm: {
                span: 16,
                offset: 8,
            },
        },
    };

    const residences = [
        {
            value: 'danang',
            label: ' Nng',
            children: [
                {
                    value: 'nguhanhson',
                    label: 'Ng Hnh Sn',
                    children: [
                        {
                            value: 'hoaquy',
                            label: 'Ho Qu',
                        },
                        {
                            value: 'hoahai',
                            label: 'Ho Hi',
                        },
                    ],
                },
            ],
        },
        {
            value: 'quangnam',
            label: 'Qung Nam',
            children: [
                {
                    value: 'tienphuoc',
                    label: 'Tin Phc',
                    children: [
                        {
                            value: 'tiencanh',
                            label: 'Tin Cnh',
                        },
                        {
                            value: 'tienhiep',
                            label: 'Tin Hip',
                        },
                    ],
                },
            ],
        },
    ];

    const prefixSelector = (
        <Form.Item name="prefix" noStyle>
            <Select style={{ width: 70 }}>
                <Option value="84">+84</Option>
                <Option value="87">+87</Option>
            </Select>
        </Form.Item>
    );

    return (
        <Row>
            <Col span={16} offset={4}>
                <Breadcrumb>
                    <Breadcrumb.Item>Trang ch</Breadcrumb.Item>
                    <Breadcrumb.Item>Thanh ton</Breadcrumb.Item>
                    <Breadcrumb.Item href="" style={CheckOutStyle.breadcrumb}>Thm a ch</Breadcrumb.Item>
                </Breadcrumb>
                <div style={CheckOutStyle.add}>
                    <p style={CheckOutStyle.addTitle}>Thm a ch</p>
                    <Form 
                        style={CheckOutStyle.form}
                        size="large"
                        {...formItemLayout}
                        form={form}
                        name="addaddress"
                        initialValues={{
                            residence: ['Thnh ph(Tnh)', 'Qun(Huyn)', 'Phng(X)'],
                            prefix: '84',
                        }}
                        scrollToFirstError>
                        <Form.Item 
                            name="name"
                            label="H v tn"
                            rules={[{ required: true, message: 'Vui lng in tn ca bn!', whitespace: true }]}
                            >
                            <Input />
                        </Form.Item>
                        <Form.Item 
                            name="address"
                            label="a ch"
                            rules={[{ required: true, type: 'array', message: 'Vui lng in a ch ca bn!'},]}>
                            <Cascader options={residences} />
                        </Form.Item>
                        <Form.Item 
                            name="street"
                            label="S nh"
                            rules={[{ required: true, message: 'Vui lng in a ch c th ca bn!', whitespace: true }]}
                            >
                            <Input />
                        </Form.Item>
                        <Form.Item
                            name="phone"
                            label="S in thoi"
                            rules={[{ required: true, message: 'Vui lng nhp s in thoi ca bn!' }]}
                            >
                            <Input addonBefore={prefixSelector} style={{ width: '100%' }} />
                        </Form.Item>
                        <Form.Item
                            name="agreement"
                            valuePropName="checked"
                            {...tailFormItemLayout}
                        >
                            <Checkbox>t lm a ch mt nh</Checkbox>
                        </Form.Item>
                        <Form.Item {...tailFormItemLayout}>
                            <Button style={CheckOutStyle.addButton} type="primary" htmlType="submit">Hon tt</Button>
                        </Form.Item>
                    </Form>
                </div>  
            </Col>
        </Row>
    )
}

export default AddAddress;