import React from "react";
import { Breadcrumb, Row, Col, Image, Typography, InputNumber, Button } from "antd";
import { CloseOutlined } from "@ant-design/icons";

import CartStyle from "./style";
import './tableStyle.css'

const data = [
    {
        foodName: 'Khoai ty chin',
        foodImg: 'KhoaiTayChien.png',
        restaurantName: 'PL Coffee & Tea',
        price: '20.000 VN',
        numberOrder: '1',
        total: '20.000 VN'
    },
    {
        foodName: 'Xc xch chin',
        foodImg: 'XucXichChien.png',
        restaurantName: 'PL Coffee & Tea',
        price: '20.000 VN',
        numberOrder: '2',
        total: '40.000 VN'
    },
    {
        foodName: 'Nem Chua Rn',
        foodImg: 'NemChuaRan.png',
        restaurantName: 'PL Coffee & Tea',
        price: '20.000 VN',
        numberOrder: '1',
        total: '20.000 VN'
    },
]

const Cart = () => {

    const { Text } = Typography;

    return (
        <Row style={{padding: "0 0 15px 15px"}}>
            <Col span={16} offset={4}>
                <Breadcrumb>
                    <Breadcrumb.Item>Trang ch</Breadcrumb.Item>
                    <Breadcrumb.Item href="" style={CartStyle.breadcrumb}>Gi hng</Breadcrumb.Item>
                </Breadcrumb>
                <table>
                    <thead>
                        <th>Sn phm</th>
                        <th>Ca hng</th>
                        <th>Gi tin</th>
                        <th>S lng</th>
                        <th>Thnh tin</th>
                        <th></th>
                    </thead>
                    {data.map(item => {
                        return (
                            <>
                            <tr>
                                <td>
                                    <Image width="100px" height="100px" src={"images/restaurant/menu/" + item.foodImg} preview={false} ></Image>
                                    <Text style={CartStyle.foodName}>{item.foodName}</Text>
                                </td>
                                <td>{item.restaurantName}</td>
                                <td style={CartStyle.price}>{item.price}</td>
                                <td><InputNumber size="large" min={1} max={20} defaultValue={item.numberOrder} /></td>
                                <td style={CartStyle.price}>{item.total}</td>
                                <td style={CartStyle.cursor}><CloseOutlined /></td>
                            </tr>
                            </>
                        )
                    })}
                </table>
                <Button style={CartStyle.tableButton}>Tip tc mua hng</Button>
                <Button style={{...CartStyle.tableButton, ...CartStyle.refreshButton}}>Cp nht gi hng</Button>
                <div style={CartStyle.bill}>
                    <div style={{display: "inline-grid"}}>
                        <Text style={CartStyle.discountText}>Nhp m gim gi</Text>
                        <div style={{display: "inline-flex"}}>
                            <input style={CartStyle.discountInput} placeholder="Nhp m ca bn vo y" />
                            <Button style={{...CartStyle.billButton, ...CartStyle.discountButton}}>Xc nhn</Button>
                        </div>
                    </div>
                    <div style={CartStyle.billForm}>
                        <p style={CartStyle.billTitle}>Thanh ton</p>
                        <div>
                            <p style={CartStyle.billText}>Tm tnh</p>
                            <p style={CartStyle.amountText}>80.000 VND</p>
                        </div>
                        <div>
                            <p style={CartStyle.billText}>Gim gi</p>
                            <p style={CartStyle.amountText}>-30.000 VND</p>
                        </div>
                        <div style={CartStyle.bd}>
                            <p style={CartStyle.billText}>Tng n hng</p>
                            <p style={CartStyle.amountText}>50.000 VND</p>
                        </div>
                        <Button style={{...CartStyle.billButton, ...CartStyle.checkoutButton}}>Tin hnh thanh ton</Button>
                    </div>
                </div>
            </Col>    
        </Row>
    )
}

export default Cart;